div.CHECKBOX {
    align-self: flex-start;
    display: flex;
    align-items: center;
    justify-content: center;

    div.CHECKBOX_COMMON_CONTAINER {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 23px;
        height: 22px;
        border-radius: 4px;
        border: solid 2px #ffffff50;
        margin: 10px 0;

        i {
            color: white;
            font-size: 12px;
        }
    }

    .alert-enter {
        opacity: 0;
        transition: all 1s;
    }

    .alert-enter-active {
        opacity: 1;
        transition: all 1s;
    }

    .alert-exit {
        opacity: 1;
        transition: all 1s;
    }

    .alert-exit-active {
        transition: all 1s;
        opacity: 0;
    }

    .item-enter {
        background-color: #2aace479;
    }

    .item-enter-active {
        background-color: #2aabe4;
        transition: background-color 500ms ease-in;
    }

    .item-enter-done {
        background-color: #2aabe4;
    }

    span.text {
        margin: 0;
        align-self: center !important;
        color: white !important;
        letter-spacing: 0;
        margin-left: 5px;
        font-weight: 500;
        font-size: 15px;
        opacity: .9;

        a {
            font-size: 12px !important;

            color: #2aabe4;
        }
    }
}