@import "../../../variable.scss";

div.SIGNUP_CONTAINER {
    padding: 0;
    background-color: rgba(93, 114, 118, 1);
    background: radial-gradient(ellipse at center, rgba(93, 114, 118, 1) 0%, rgba(13, 44, 51, 1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#5d7276', endColorstr='#0d2c33', GradientType=1);

    min-height: 100vh;
    backdrop-filter: blur(70px);

    div.row {
        padding: 0;
        margin: 0;

        div.COVER_TITLE {
            padding: 0;
            width: 100%;

            img {
                height: 100%;
                width: 100%;
            }
        }

        div.MOBILE_TITLE {

            display: flex;
            align-items: center;
            justify-content: center;
            padding: 20px;

            img {
                width: 220px;
                height: 32px;
            }
        }

        div.LOGIN_FORM {
            padding-top: 30px !important;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;

            img {
                width: 60%;
                margin-bottom: 40px;
            }

            form {
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;

                span {
                    font-family: Roboto;
                    font-size: 11px;
                    text-align: left;
                    align-self: flex-start;
                    color: #e26868;
                    letter-spacing: 1px;
                }

                button {
                    width: 100%;
                    height: 43px;
                    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
                    background-color: $PRIMARY_COLOR;
                    border: none;
                    border-radius: 3px;
                    color: white;
                    font-size: 12px;
                    font-family: Roboto;
                    letter-spacing: 1px;
                    margin-top: 10px;
                    outline: none;

                }

                .disabled {
                    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16) !important;
                    background-color: #77797a !important;
                }

                a {
                    text-decoration: underline;
                    font-family: Roboto;
                    color: $PRIMARY_COLOR;
                    font-size: 13px;
                    margin-top: 20px;
                }

                div.Or {
                    margin-top: 20px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    padding: 10px 0;

                    div.line {
                        width: 160px;
                        border-bottom: 1px solid $PRIMARY_COLOR;
                    }

                    p {
                        margin: 0 20px;
                        font-family: Roboto;
                        color: white;
                        font-size: 14px;
                        opacity: .7;
                    }
                }

                div.SignUp {
                    margin-top: 20px;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    p {
                        margin: 0 3px;
                        font-family: Roboto;
                        color: white;
                        font-size: 14px;
                        opacity: .7;
                    }

                    a {
                        margin: 0;
                        font-family: Roboto;
                        font-size: 14px;
                        opacity: .7;
                    }
                }

            }
        }
    }
}